import React from "react";
import data from "../../static/data.json";
import { isDoubleExotus, addZeroes } from '../utils/helpers';
import "../style/table.css";

for (let i=0; i < data.length; i++) {
    data[i].play = data[i].team1 + ' ' + data[i].team2;
}



// Получаем все виды спорта в текущих данных
let sports = Array.from(data.reduce((acc, elem) => acc.add(elem.typeSport), new Set()));

const IndexPage = () => (
    <div className='pageWrapper'>
        <div className='page'>
            <div className='row'>
                <img alt='header' src= {'/header.png'} className='header' />
            </div>
            <div className='row row-paddings'>
                <div className='content'>
                    <img alt='search-nav' src= {'/search-nav.png'} className='search-nav' />
                    { sports.map((sport, index) => <Sport key={index} {...{sport}}/>) }
                </div>
                <div className='right-col'>
                    <img alt='right-col' src= {'/right-col.png'} className='right-col' />
                </div>
            </div>
            <div className='row'>
                <img alt='footer' src= {'/footer.png'} className='footer' />
            </div>
        </div>
    </div>
)

const Sport = ({ sport }) => {
    let matches = Array.from(data.filter( item => item.typeSport === sport ).reduce((acc, elem) => acc.add(elem.match), new Set()));
    
    return (
        <div className='sport'>
            <div className='sport-title'><b>{sport}</b></div>
                { matches.map((match, index) => <Match key={index} {...{sport, match}}/>) }
        </div>
    )
}


const Match = ({ sport, match }) => {
    
    let plays = Array.from(
            data.filter( item => (item.typeSport === sport && item.match === match) )
                .reduce((acc, elem) => acc.add(elem.play), new Set())
        );

    return (
        <div className='match'>
            <Header match={match} isDoubleExotus={isDoubleExotus(sport)} />      
                { plays.map((play, index) => <Play key={index} {...{sport, match, play}}/>) }
        </div>
    )
}

const Play = ({ sport, match, play }) => {
    return (
        <div className='play'>
            {
                data.filter( item => (item.typeSport === sport && item.match === match && item.play === play)).map(
                    (item, index) => {
                        return <Row key={index} isDoubleExotus={isDoubleExotus(sport)} isSubEvent={index} {...item} />
                })
            }
        </div>
    )
}

const Header = ({match, isDoubleExotus}) => {
    return (
        <div className='row header-match'>
            <div className={'row-side row-left-side-' + +isDoubleExotus}>
                <div className='col teams'><b>{match}</b></div>
            </div>
            <div className={'row-side row-right-side-' + +isDoubleExotus}>
                <div className='col coef'>1</div>

                { isDoubleExotus && <div className='col coef'>X</div> }

                <div className='col coef'>2</div>

                { isDoubleExotus && <div className='col coef'>1X</div>}
                { isDoubleExotus && <div className='col coef'>12</div>}
                { isDoubleExotus && <div className='col coef'>2X</div>}

                <div className='col coef'>1</div>     
                <div className='col coef'>Фора</div>                 
                <div className='col coef'>2</div>              
                <div className='col coef'>Б</div>    
                <div className='col coef'>Тотал</div>              
                <div className='col coef'>М</div>       
                <div className='col coef'>...</div>     
            </div>                                                    
        </div>
    );
};

const Row = ({ isSubEvent, isDoubleExotus, typeSport, match, team1, team2, part, score, time,
    one, x, second, oneX, oneSecond, secondX, fSign1, f, f1, f2, total, more, less, other }) => {

    let sideCell = (!isSubEvent) ?  `${team1} — ${team2}` : part;
    
    return (
        <div className='row'>
            <div className={'row-side row-left-side-' + +isDoubleExotus}>
                <div className={'col teams' + (isSubEvent ? ' part' : '')}>{sideCell}</div>
                <div className='col timeScore'>{score + '  '} <b>{time}</b></div>
            </div>
            <div className={'row-side row-right-side-' + +isDoubleExotus}>
                <div className='col coef'>{ addZeroes(one) }</div>

                { isDoubleExotus && <div className='col coef'>{addZeroes(x)}</div> }

                <div className='col coef'>{addZeroes(second)}</div>

                { isDoubleExotus && <div className='col coef'>{addZeroes(oneX)}</div> }
                { isDoubleExotus && <div className='col coef'>{addZeroes(oneSecond)}</div> }
                { isDoubleExotus && <div className='col coef'>{addZeroes(secondX)}</div> }

                <div className='col coef gray-text'>{fSign1} {f}</div>
                <div className='col coef'>{addZeroes(f1)}</div>          
                <div className='col coef'>{addZeroes(f2)}</div>              
                <div className='col coef'>{addZeroes(more)}</div>    
                <div className='col coef gray-text'>{total}</div>              
                <div className='col coef'>{addZeroes(less)}</div>       
                <div className='col coef'>{other}</div> 
            </div>                                     
        </div>
    );
};

export default IndexPage
